*{
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
}

.SignInBackground{
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100%;
    background-color: #FFFFFF;
    margin-top: 10px;
}
.SignInPoppup{
    box-sizing: border-box;
    width: calc(600/1440 * 100vw);
    height: calc(400/1440 * 100vw);
    margin-left: 0;
    margin-right: 0;
    margin-top: calc(125/1440 * 100vw);
    background: #FFFFFF;
    box-shadow: 0px 0px 40px 10px rgba(0, 0, 0, 0.25);
    align-items: center;
    align-content: center;
    text-align: center;
}
.SignInPoppupHeder{
    margin-top: calc(26/1440 * 100vw);
    margin-bottom: calc(17/1440 * 100vw);
    font-style: normal;
    font-weight: 700;
    font-size: calc(50/1440 * 100vw);
    line-height: calc(50/1440 * 100vw);
    color: #787878;
}
.SignInPoppupSubtitle{
font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: calc(16/1440 * 100vw);
line-height: calc(16/1440 * 100vw);
margin-bottom: 3px;
color: #787878;
text-align: left;
margin-left: calc(53/1440 * 100vw);
}
.SignInPoppupStyleInput{
    box-sizing: border-box;
    margin-left: 0 auto;
    margin-right: 0 auto;
    width: calc(509/1440 * 100vw);
    height: calc(49/1440 * 100vw);
    background: #FFFFFF;
    border: 2px solid #787878;
}
.SignInPoppupStyleInput::placeholder{
    color: #CCCCCC;
}

.SignInPoppupLogin{
    margin-bottom: calc(11/1440 * 100vw);
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: calc(28/1440 * 100vw);
    line-height: calc(36/1440 * 100vw);
    color: #787878;
}
input[type="email" i] {
    padding-left: calc(15/1440 * 100vw);
}
.SignInPoppupPassword{
    margin-bottom: calc(52/1440 * 100vw);
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: calc(28/1440 * 100vw);
    line-height: calc(36/1440 * 100vw);
    color: #787878;
}
input[type="password" i] {
    padding-left: calc(15/1440 * 100vw);
}
.SignInPoppupButtonEnter{
    box-sizing: border-box;
    width: calc(180/1440 * 100vw);
    height: calc(52/1440 * 100vw);
    background: #1B2473;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: calc(24/1440 * 100vw);
    line-height: calc(16/1440 * 100vw);
    color: #FFFFFF;
    border: 0;
}
@media screen and (max-device-width: 768px){
    .poppup{
        box-sizing: border-box;
        width: calc(600/768 * 100vw);
        height: calc(383/768 * 100vw);
        margin-left: 0;
        margin-right: 0;
        margin-top: calc(80/768 * 100vw);
        background: #FFFFFF;
        box-shadow: 0px 0px 40px 10px rgba(0, 0, 0, 0.25);
        align-items: center;
        align-content: center;
        text-align: center;
    }
    .poppup__heder{
        margin-top: calc(26/768 * 100vw);
        margin-bottom: calc(17/768 * 100vw);
        font-style: normal;
        font-weight: 700;
        font-size: calc(50/768 * 100vw);
        line-height: calc(50/768 * 100vw);
        color: #787878;
    }
    .poppup__subtitle{
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: calc(16/768 * 100vw);
        line-height: calc(16/768 * 100vw);
        margin-bottom: 3px;
        color: #787878;
        text-align: left;
        margin-left: calc(53/768 * 100vw);
    }
    .poppup__style-input{
        box-sizing: border-box;
        margin-left: 0 auto;
        margin-right: 0 auto;
        width: calc(509/768 * 100vw);
        height: calc(49/768 * 100vw);
        background: #FFFFFF;
        border: 2px solid #787878;
    }
    .poppup__login{
        margin-bottom: calc(11/768 * 100vw);
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: calc(28/768 * 100vw);
        line-height: calc(36/768 * 100vw);
        color: #787878;
    }
    .poppup__password{
        margin-bottom: calc(52/768 * 100vw);
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: calc(28/768 * 100vw);
        line-height: calc(36/768 * 100vw);
        color: #787878;
    }
    .poppup__button-enter{
        box-sizing: border-box;
        width: calc(180/768 * 100vw);
        height: calc(52/768 * 100vw);
        background: #1B2473;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: calc(24/768 * 100vw);
        line-height: calc(16/768 * 100vw);
        color: #FFFFFF;
}
}
