        width: calc(17.5/768*100vw);
        height: calc(17.5/768*100vw);
        margin-left: calc(5/768*100vw);
    }
    .HederShopSearchInput{
        border: 0;
        background-color: #EDEDF0;
        width: calc(350/768*100vw);
        height: calc(32/1920*100vw);
        margin-left: calc(11.5/1920*100vw);
        margin-right: calc(3/1920*100vw);
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: calc(20/1920*100vw);
