    }
    .HederShopSearchInput::-webkit-search-cancel-button {
        align-items: center;
        -webkit-appearance: none;
        background: url(../Img/Delete.svg);
        background-size: calc(14/1920*100vw);
        width: calc(14/1920*100vw);
        height: calc(14/1920*100vw);
        mask-image: #2979FF;
        cursor: pointer;
    }
    .HederShopSearchButton{
        display: flex;
        justify-content: center;
        padding-left: 0;
        padding-right: 0;
        border: 0;
        align-items: center;
        width: calc(14/1920*100vw);
        height: calc(14/1920*100vw);
        margin-right: calc(14/1920*100vw);
        margin-left: calc(3/1920*100vw);
        cursor: pointer;
    }
    .HederShopSearchButtonImg *{
        align-items: center;
        padding-left: 0;
        margin-left: 0;
        width: calc(14/1920*100vw);
        height: calc(14/1920*100vw);
        fill: #2979FF;
    
    }
    .HederShopLoginConteiner{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 45%;
