        margin-left: calc(1/1920*100vw);
        margin-right: calc(20/1920*100vw);
    }
    .HederShoploginButton{
        display: flex;
        justify-content: center;
        width: calc(104/1920*100vw);
        height: calc(40/1920*100vw);
        border: calc(1/1920*100vw) solid #9DC2FF;
        margin-left: calc(15/1920*100vw);
        padding: calc(1/1920*100vw) calc(6/1920*100vw);
        border-radius: calc(4/1920*100vw);
        background-color: white;
        font-family: 'Quicksand';
        font-style: normal;
        font-weight: 700;
        font-size: calc(20/1920*100vw);
        align-items: center;
        justify-content: center;
        text-align: center;
        letter-spacing: 0.0075em;
        color: #2264D1;
        cursor: pointer;
    
    }
    .HederShoploginButtonSize{
        width: calc(52/1920*100vw);
        height: calc(40/1920*100vw);
    }
    .HederShopBasketButtonImg {
        align-items: center;
        padding-left: 0;
        margin-left: 0;
        width: calc(25/1920*100vw);
